.main-table {
    width: 90vw;
    border-collapse: collapse;
    margin-left: 5rem;
    position: sticky;
    overflow-y: scroll;
    padding-bottom: 2rem;
    margin-bottom: 2rem;
    z-index: 6;
  }
  .top-bar{
    width: fit-content;
    border-collapse: collapse;
    display: flex;
    justify-content: space-between;
    width: 90vw;
    position: sticky;
    margin-bottom: 3px;
    margin-left: 5rem;
    margin-top: 20px;
  }
  .Search{
    width: 40%;
  }
  .new-btn{
    background-color: black;
    color: aliceblue;
    border-radius: 5px;
  }
  .export-btn{
    width: 8%;
    padding:3px
  }
  .hide-table{
    width: 100%;
    border-collapse: collapse;
    font-size: 13px;
  }
  .main-table{
    border: 1px solid black;
  }
  
  .main-table th{
    padding: 10px;
    text-align: left;
  }
  .main-table td{
    padding: 10px;
    text-align: left;
  }
  
  tbody tr:hover {
    background-color: #f5f5f5;
  }
  
  .expanded-row {
    background-color: #ebe9e9;
  }
  