/* Package.css */
.form-Package {
  width: 80vw;
  display: flex;
  gap: 20px;
  padding: 10px;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
  margin: 1rem;
  margin-left: 5rem;
}

.left-form {
  flex: 1;
  padding: 12px;
  box-sizing: border-box;
}
.packege-label {
  text-align: left;
  color: #4c4e64;
  font-weight: 400;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

.right-form {
  flex: 1;
  padding: 12px;
  padding-right: 1.5rem;
  box-sizing: border-box;
}

.input-group {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.input-group input {
  flex: 1;
  padding: 10px;
}

.full-input {
  width: 100%;
  padding: 10px;
  padding-right: 2rem;
  border: 1px solid rgb(174, 170, 170);
  border-radius: 10px;
}

.right-input {
  width: 100%;
  padding: 10px;
  margin-top: 12px;
}

.Heading {
  margin-bottom: 20px;
}

.left-form input {
  margin-top: 12px;
}
.custom-input {
  border: 1px solid rgb(141, 134, 134);
  margin: 5px;
}

/* Media Query  for Mobile and Tablet */

@media only screen and (max-width: 920px) {
  .form-Package {
    gap: 15px;
    margin-left: 5rem;
  }

  .left-form,
  .right-form {
    width: 50%;
    margin: 0;
  }
}

@media only screen and (max-width: 640px) {
  .form-Package {
    flex-direction: column;
    gap: 10px;
    margin-left: 5rem;
  }

  .left-form,
  .right-form {
    width: 100% !important;
    margin: 0;
  }

  .input-group input,
  .full-input {
    flex: 1;
  }

  .right-input {
    width: 100%;
    margin-top: 10px;
  }
}
