.Sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 88vh;
  background-color: black;
  color: aliceblue;
  width: 4rem;
  padding-block: 2rem;
  border-radius: 12px;
  margin-left: 10px;
  margin-block: 10px;
  transition: 0.5s;
  z-index: 10;
  opacity: .9;
  font-size: 14px;
  position: fixed;
}

.Sidebar.active {
  width: 14rem;
  padding-block: .8rem;
  margin-block: 5px;
  height: 96vh;
  background-color: black;
  opacity: .85;
}

.Sidebar-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2.5rem;
}
.Actions{
  display: flex;
}
.email{
  color: blue;
  text-decoration: underline;
}
.btn-active{
  background-color: rgb(190, 244, 226);
  color: rgb(44, 223, 97);
  border: none;
  border-radius: 8px;
  padding: 2px;
  padding-inline: 3px;
}
.btn-Pending{
  background-color: rgb(243, 202, 210);
  color: rgb(224, 81, 42);
  border: none;
  border-radius: 8px;
  padding: 2px;
  padding-inline: 3px;

}
.sidebar-img{
  width: 4rem;
  height: 4rem;
}
.Sidebar-Options {
  display: flex;
  flex-direction: column;
  gap: .2rem;
  margin-bottom: 10rem;
}

.Sidebar-Option{
  display: flex;
  gap: 5px;
  margin: 12px;
}
.sidebar-img{
  width: .8rem;
  height: .8rem;
}
.Sidebar-feature {
  display: flex;
  gap: 1rem;
  margin-block: 10px;
  margin-inline: 10px;
}
.feature-img{
  margin-inline: 5px;
}

.dropdown{
  text-align: left !important;
  margin: 0 !important;
  padding: 0!important;
}
.text {
  margin: 0;
  padding: 0;
}

.text li {
  margin: 0;
  padding: 0;
  margin-left: 12px;
  padding: 5px;
}
.Hide {
  display: none;
}

.Show {
  display: block;
}
