.Accounts-main {
    flex-grow: 1;
    width: 80vw;
    margin-left: 5rem;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    width: 90%;
    padding: 1.5rem;
    box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
    margin: 1rem;
    border-radius: 12px;
    margin-top: 5px;
  }
  
  .form-row {
    display: flex;
    justify-content: center;
  }
  .from-col-div{
    width: 70%;
    position: relative;
  }
  
  .form-label {
    position: absolute;
    top: -0px;
    left: 8%;
    color: #999;
    background-color: rgb(255, 255, 255);
    font-size: 12px;
  }
  
  .from-col1,
  .from-col2 {
    width: 86%;
    padding: 10px;
    margin: 10px;
    border-radius: 10px;
    border: 1px solid rgb(165, 156, 156);
  }
  
  .psd-btn{
    background-color: rgb(36, 35, 35);
    padding: 8px;
    border-radius: 8px;
    color: aliceblue;
    border: 1px solid rgb(165, 156, 156);
    margin-right: 2%;
  }
  .cnl-btn{
    padding: 8px;
    border-radius: 8px;
    border: 1px solid rgb(165, 156, 156);
    margin-right: 70%;
  }
  .txt{
    color: #999;
    font-size: 1.2rem;
    font-weight: 600;
    text-align: left;
  }
  .agree{
    display: flex;
    gap: 1rem;
  }
  .DA-btn{
    padding: 8px;
    width: fit-content;
  }