.preview-div {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 20px;
  gap: 1rem;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
  margin: 1rem;
  margin-left: 5rem;
  justify-content: left;
  width: 83vw;
}

.btn {
  background-color: black;
  color: white;
  border-radius: 8px;
  border: 1px solid;
  padding: 8px;
  width: fit-content;
}
.priview-txt {
  text-align: left;
  color: rgb(76, 78, 100, 0.6);
  font-family: Inter;
  font-style: normal;
}
.priview-check {
  margin-left: 0;
  display: flex;
  justify-content: left;
}
