.main-div{
    width: 80vw;
    padding: 20px;
 
}

.main-div{
    margin-bottom:8px ;
    box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
    margin: 1rem;
    margin-left: 5rem;
}

.button-div{
padding-top: 12px;
}

.proceed-btn{
    background-color: black;
    color: white;
    padding: 8px;
    border-radius: 8px;
    margin-right: 8px;
}

.cancel-btn{
    background-color: white;
    color: black;
    padding: 8px;
    border-radius: 8px;
}

.input-group input{
    margin-bottom: 12px;
    border-radius: 8px;
}

.full-width-input{
    border-radius: 8px;
    width: 98%;
}


/* Media Query  for Mobile and Tablet */
@media (max-width: 768px) {
    .form-container {
      flex-direction: column;
    gap: 10px;
    margin-left: 1rem;
    margin-right: 1rem;
    }
  
    .left-form, .right-form {
      width: 100% !important;  
    margin: 0;
    }
  
    .input-group input, .full-width-input {
      flex: 1;
    }
  
    .right-input {
      width: 100%;
      margin-top: 10px;
    }
  }
  
  @media (max-width: 1024px) {
    .form-container {
      gap: 15px;
    }
  
    .left-form, .right-form {
      width: 50%;
      margin: 0;
    }
  }
  